<template>
  <div class="sticky-top">
    <div class="nav-content">
      <div class="container d-flex justify-content-between">
        <div class="nav-open-btn"
            :class="[isOpenNav? 'nav-close-btn': '']"
            @click="isShowNav">
          <span></span>
          <span></span>
          <span></span>
        </div>
        <div class="logo bg-setting"></div>
        <div class="list-content" :style="isOpenNav? showList : ''">
          <ul class="list">
            <li @click="isShowNav">
              <router-link to="/" class="list-item">{{ $t('Home')}}</router-link>
            </li>
            <li @click="isShowNav">
              <router-link to="/product/automation" class="list-item">{{ $t('Automation')}}</router-link>
            </li>
            <li @click="isShowNav">
              <router-link to="/product/hydropower" class="list-item">{{ $t('Hydropower')}}</router-link>
            </li>
            <li @click="isShowNav">
              <router-link to="/product/vibrationTransport" class="list-item">{{ $t('VibrationTransport')}}</router-link>
            </li>
            <li @click="isShowNav">
              <router-link to="/contact" class="list-item">{{ $t('ContactUs')}}</router-link>
            </li>
            <li class="translation" @click="hideNav">
              <label
                v-for="(item, index) in optionsLang"
                v-bind:key="index"
                class="changeLanguage"
              >
                <input
                  type="radio"
                  class="change-radio"
                  v-model="$store.state.lang"
                  :value="item.value"
                  @change="setLang(item.value), emitLangChange(item.value)"
                  > {{ item.text }}
              </label>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Navbar',
  data(){
    return{
      isOpenNav: false,
      deviceResolution: 0,
      showList: {
        right: '0px'
      },
      optionsLang: [
        { text: '中文', value: 'zh' },
        { text: 'English', value: 'en' }
      ]
    }
  },
  methods:{
    isShowNav(){
      console.log('isShowNav')
      this.isOpenNav = !this.isOpenNav;
    },
    hideNav(){
      console.log('hideNav')
      this.isOpenNav = false
    },
    setLang (value) {
      this.$store.commit('setLang', value);
      this.$i18n.locale = value;
      localStorage.setItem('footmark-lang', value);
    },
    emitLangChange(value){
      this.$emit('changeLanguage', value)
    }
  }
}
</script>
<style lang="scss">
@import '../assets/scss/all';

.nav-content{
  width: 100%;
  height: 80px;
  background-color: #fff;
  box-shadow: 0 10px 20px -10px rgba(0,0,0,0.2);
  @include screen($mobile){
    height: 50px;
  }
}
.nav-open-btn{
  display: none;
  @include screen($mobile){
    display: block;
    width: 50px;
    height: 50px;
    position: absolute;
    right: 10px;
    padding: 10px 8px;
    span{
      display: block;
      width: 100%;
      height: 5px;
      background-color: gray;
      margin-bottom: calc((30px - 15px) / 2);
      transition-duration: .3s;
      transition-timing-function: cubic-bezier(0.43, 0.01, 0.61, 1.01);
      &:last-child{
        margin-bottom: 0;
        opacity: 1;
      }
    }
  }
}
.nav-close-btn{
  display: none;
  @include screen($mobile){
    display: block;
    width: 50px;
    height: 50px;
    position: absolute;
    right: 10px;
    padding: 5px 8px;
    span{
      display: block;
      height: 5px;
      background-color: gray;
      position: absolute;
      width: 40px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(225deg);
      &:nth-child(2){
        transform: translate(-50%, -50%) rotate(135deg);
      }
      &:nth-child(3) {
        opacity: 0;
      }
    }
  }
}
.logo{
  width: 150px;
  height: 80px;
  background-image: url('../assets/ATSC-logo_new.png');
  background-size: contain !important;
  @include screen($mobile){
    width: 100px;
    height: 50px;
    margin: 0 auto;
  }
}
.list-content{
  height: 80px;
  background-color: white;
  @include screen($mobile){
    transition-duration: .3s;
    margin-top: 50px;
    position: absolute;
    top:0;
    right: 100%;
    width: 100vw;
    height: 100vh;
    z-index: 99;
  }
}
.list{
  padding: 0;
  margin: 0;
  list-style: none;
  li{
    padding: 31px 15px;
    float: left;
    .list-item{
      display: block;
      font-size: 1.125rem; //18px
      line-height: 1;
      text-align: center;
      color: #737373;
      text-decoration: none;
      &.router-link-exact-active{
        color: #14213D;
        font-weight: bold;
      }
      &.router-link-exact-active::after{
        @include screen($mobile){
          content: '';
          display: block;
          padding-bottom: 10px;
          border-bottom: 2px solid #14213D;
          width: 100px;
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }
    @include screen($mobile){
      padding: 20px;
      float: none;
    }
  }
}

.translation{
  @include screen($mobile){
    text-align: center;
  }
}
.changeLanguage{
  padding: 0 10px;
  cursor: pointer;
  font-size: 14px;
  line-height: 1;
  color: #737373;
  &:first-child{
    &::before{
      content: '';
      display: block;
      width: 1px;
      height: 20px;
      background-color: rgba(#737373, 0.75);
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
    }
  }

  @include screen($mobile){
    padding: 10px;
    font-size: 16px;
    line-height: 1.5;
  }
}

input.change-radio[type="radio"]{
  position: absolute;
  visibility: hidden
}
</style>